
import { reactive, toRefs, ref, onMounted, onActivated, onUpdated } from "vue"
import * as _ from "lodash"

export default {
  name: "OperatingCard",
  emits: ["leftBtn", "rightBtn"],
  props: {
    leftText: {
      type: String,
      default: "删除",
    },
    rightText: {
      type: String,
      default: "编辑",
    },
  },
  setup(props: any, context: any) {
    const state = reactive({})

    const leftBtn = () => {
      context.emit("leftBtn")
    }
    const rightBtn = () => {
      context.emit("rightBtn")
    }

    return {
      ...toRefs(state),
      leftBtn,
      rightBtn,
    }
  },
}
